.nav-style {
  /* params: border width, style, color */
  /* border: 2px solid red; */
  background-color: rgb(0, 0, 0);
  background-color: rgba(255, 255, 255, 0.700);
  /* remove container-fluid extra right padding */
  /* padding: 0; */
  display: flex;
  /* justifies content based on horizontal axis */
  justify-content: space-between;
   /* center the elements based on vertical axis */
  align-items: center;
  height: 4rem;
  padding: 3rem 72px !important;
  position: fixed;
  /* opacity: 10%; */
  z-index: 100;
}

.nav-heading {
  /* params: top && bottom left && right */
  padding: 0.15rem 1.0rem;
  /* aligns the content (i.e. text and other inline or inline-block elements in the container) between left and right edges */
  text-align: center;
  /* align the middle of the button element with the middle of lowercase letters in the parent. */
  vertical-align: middle;
  /* remove any underlines, line-through, overline or inheritted decorations */
  text-decoration: none !important;
  /* sets the element inline with the rest of the text around it. Unlike inline atrribute you can set width and height */
  display: inline-block;
  font-size: 1.2rem;
  /* sets a equal amount of height from the top and bottom of the text */
  line-height: 1.5;
  /* params: border width, style, color used it to see border of the element*/
  /* border: 1px solid black; */
  color: rgba(255, 255, 255, 0.733);
  color: rgba(0, 0, 0, 0.733);
  font-family: Arial, Helvetica, sans-serif;
}

